*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

:root{
    --bgcolor: #ebe5e9;
    --text-color: #1e2a5a;
    --logo-primary-color: #ff5b00;
    --logo-color: #016ca5;
    --card-bg-color: #f9f9ff;

    --container-width-lg: 80%; /* large device */
    --container-width-md: 90%; /* medium device */
    --container-width-sm: 95%; /* small device */

    --section_gap_medium: 4.3rem;
}

body{
    font-size: 16px;
    font-family: "Roboto", sans-serif;
    color: #7b838a;
 }

.container{
    margin: 0 auto;
}

p{
    margin-bottom: 0%;
    font-family: "Roboto", sans-serif;
    font-size: 1rem;
}

a{
    text-decoration: none;
}

.section_gap{
    padding-top: 8.1rem;
}

.row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px
}

h1, h2, h3, h4, h5, h6 {
    font-family: "Rubik", sans-serif;
    font-weight: 500;
    color: #002347;
}

.primary-btn {
    display: inline-block;
    background: #fdc632;
    padding: 0px 48px;
    color: #002347;
    font-size: 13px;
    font-weight: 500;
    line-height: 50px;
    border-radius: 5px;
    outline: none !important;
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
    -webkit-transition: all 0.3s ease 0s;
    -moz-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s; }
    .primary-btn:hover {
      background: #002347;
      color: #fdc632; }
  
  .primary-btn2 {
    display: inline-block;
    background: #002347;
    padding: 0px 48px;
    color: #fdc632;
    font-size: 13px;
    font-weight: 500;
    line-height: 48px;
    border-radius: 5px;
    border: 1px solid transparent;
    outline: none !important;
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
    -webkit-transition: all 0.3s ease 0s;
    -moz-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s; 
    }
.primary-btn2:hover {
      background: transparent;
      color: #002347;
      border: 1px solid #002347; }
  

